import { Avatar, Box, Button, Typography } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import { resolveTicketApi } from "../../../api/Tickets";
import ConfirmationModal from "../../common/ConfirmationModal";
import getInitials from "../../../utils/getInitials";
import { useEffect, useState } from "react";
import toast from "react-hot-toast";
import { loadingActions } from "../../../slices/Loading";
import { TicketActions } from "../../../slices/TicketSlice";

const ChatThreadToolbar = (props) => {
  const { ticket } = props;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isResolved, setIsResolved] = useState(false);
  const reduxUsers = useSelector((state) => state.UData.users);
  const reduxDrivers = useSelector((state) => state.UData.drivers);
  const [userDetail, setUserDetail] = useState(undefined);

  // Sets new value for Resolved
  const handleResolveConfirm = () => {
    setIsResolved(true);
  };

  useEffect(()=>{
    if(ticket?.createdBy){
      if(reduxUsers[ticket.createdBy]){
        setUserDetail(reduxUsers[ticket.createdBy])
      }else if(reduxDrivers[ticket.createdBy]){
        setUserDetail(reduxDrivers[ticket.createdBy])
      }
    }
  },[reduxUsers, reduxDrivers])

  // Close Confirmation
  const handleCloseConfirm = () => {
    setIsResolved(false);
  };
  //console.log(resolveTicketApi)
  const handleResolved = async () => {
    try {
      dispatch(loadingActions.increment());
      const resolve = await resolveTicketApi(ticket.id);
      dispatch(TicketActions.setTicketStatus({
        ticketId : ticket.id,
        status: 2
      }))
      setIsResolved(true)
      
      toast.success("Ticket Resolved Successfully.");
      navigate("/tickets");
    } catch (error) {
      if (error.response.data && error.response.data.message) {
        toast.error(error.response.data.message)
      } else {
        toast.error("Oops! Something Went Wrong.");
      }
      console.log(error)
    } finally {
      dispatch(loadingActions.decrement());
    }
  };

  return (
    <>
      <Box
        sx={{
          alignItems: "center",
          backgroundColor: "background.paper",
          borderBottom: (theme) => `1px solid ${theme.palette.divider}`,
          display: "flex",
          justifyContent: "space-between",
          flexShrink: 0,
          minHeight: 64,
          px: 2,
          py: 1,
        }}
      >
        <Box
          sx={{
            alignItems: "center",
            display: "flex",
          }}
        >
          <Avatar
            src={userDetail?.profilePic}
            sx={{
              height: 42,
              width: 42,
            }}
          >
            {getInitials(userDetail?.firstName)}
          </Avatar>
          <Typography color="textPrimary" sx={{ ml: 2 }} variant="subtitle2">
            {userDetail?.firstName +
              " " +
              userDetail?.lastName}
          </Typography>
        </Box>
        {
          ticket?.ticketStatus === 2 ?
            <Box sx={{ background: '#fff', color: '#3BAE39', fontWeight: '500' }}>Resolved</Box>
            : 
            <Button
              disabled={ticket?.ticketStatus === 2}
              sx={{ background: "#fff", color: "#3BAE39" }}
              onClick={() => {
                handleResolveConfirm();
              }}
            >
            Resolve
            </Button>
        }
      </Box>

      <ConfirmationModal
        open={isResolved}
        handleClose={handleCloseConfirm}
        handleConfirm={handleResolved}
        title={"Resolve Ticket"}
        description={"Confirm ticket resolved ?"}
        cancleButtonName={"No"}
        confirmButtonName={"Yes"}
      />
    </>
  );
};

export default ChatThreadToolbar;
