/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef } from "react";
import PropTypes from "prop-types";
import { Box } from "@material-ui/core";
// import useAuth from "../../../hooks/useAuth";
import Scrollbar from "../../Scrollbar";
import ChatMessage from "./ChatMessage";

const ChatMessages = (props) => {
  const { ticket } = props;
  const rootRef = useRef(null);
  // const { user } = useAuth();
  // let senderAvatar;
  let senderName;
  let senderType;

  const scrollToBottom = () => {
    if (rootRef?.current?._container) {
      rootRef.current._container.scrollTop =
        rootRef.current._container.scrollHeight;
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [ticket, scrollToBottom]);

  return (
    <Scrollbar options={{ suppressScrollX: true }} ref={rootRef}>
      <Box sx={{ p: 2 }}>
        {ticket?.messages.map((message) => {
          const participant = message.sendBy;
          {/* console.log(message.sendBy, JSON.parse(localStorage.getItem('User')).id); */}
          if (JSON.parse(localStorage.getItem('User'))?.id && participant === JSON.parse(localStorage.getItem('User')).id) {
            senderName = "";
            senderType = "admin";
          } else {
            senderName = "";
            senderType = "user";
          }
          return (
            <ChatMessage
              createdAt={message.createdAt}
              message={message.message}
              key={message.ticketMessageId}
              senderName={senderName}
              senderType={senderType}
            />
          );
        })}
      </Box>
    </Scrollbar>
  );
};

// ChatMessages.propTypes = {
//   messages: PropTypes.array,
//   participants: PropTypes.array,
// };

export default ChatMessages;
