import { useParams } from "react-router-dom";
import { Box, Divider } from "@material-ui/core";
import { useSelector, useDispatch } from "../../../store";
import ChatMessageAdd from "./ChatMessageAdd";
import ChatMessages from "./ChatMessages";
import ChatThreadToolbar from "./ChatThreadToolbar";
import { sendMessageApi } from "../../../api/Tickets";
import { TicketActions } from "../../../slices/TicketSlice";

const ChatThread = () => {
  // Convert string id to number
  let { id } = useParams();
  let idNum = Number(id);
  const dispatch = useDispatch();

  // get Api data
  const reduxTicket = useSelector((state) => state.ticket.ticket);

  // handle send Message
  const handleSendMessage = async (body) => {
    try {
      const newMessage = await sendMessageApi(body, idNum);
      dispatch(
        TicketActions.sendMessage({ newMessage: newMessage, id: idNum })
      );
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Box
      sx={{
        backgroundColor: "background.default",
        display: "flex",
        flexDirection: "column",
        flexGrow: 1,
      }}
    >
      <ChatThreadToolbar ticket={reduxTicket[idNum]} />
      <Box
        sx={{
          flexGrow: 1,
          overflow: "auto",
        }}
      >
        <ChatMessages ticket={reduxTicket[idNum]} />
      </Box>
      <Divider />
      <ChatMessageAdd
        disabled={false}
        ticketId={idNum}
        onSend={handleSendMessage}
      />
    </Box>

    
  );
};

export default ChatThread;
