import { useState } from "react";
import PropTypes from "prop-types";
import { formatDistanceToNowStrict } from "date-fns";
import { Lightbox } from "react-modal-image";
import { Box, Typography } from "@material-ui/core";

const ChatMessage = (props) => {

  const { createdAt, senderType, message } = props;
  const [expandMedia, setExpandMedia] = useState(false);

  return (
    <Box
      sx={{
        display: "flex",
        mb: 2,
      }}
    >
      <Box
        sx={{
          display: "flex",
          flexDirection: senderType === "user" ? "row" : "row-reverse",
          maxWidth: 500,
          ml: senderType === "user" ? 0 : "auto",
        }}
      >
        <div>
          <Box
            sx={{
              backgroundColor:
                senderType === "user" ? "background.paper" : "primary.main",
              borderRadius: 3,
              boxShadow: 1,
              color:
                senderType === "user" ? "text.primary" : "primary.contrastText",
              px: 2,
              py: 1,
              width: "fit-content",
              marginLeft: 
                senderType === "user" ? "" : "auto",
            }}
          >
            {message}
          </Box>
          <Box
            sx={{
              display: "flex",
              justifyContent: senderType === "user" ? "flex-start" : "flex-end",
              mt: 1,
              px: 2,
            }}
          >
            <Typography color="textSecondary" variant="caption">
              {formatDistanceToNowStrict(new Date(createdAt))} ago
            </Typography>
          </Box>
        </div>
      </Box>
      {expandMedia && (
        <Lightbox large={"body"} onClose={() => setExpandMedia(true)} />
      )}
    </Box>
  );
};

ChatMessage.propTypes = {
  body: PropTypes.string,
  contentType: PropTypes.string,
  createdAt: PropTypes.number,
  senderAvatar: PropTypes.string,
  senderName: PropTypes.string,
};

export default ChatMessage;
